import React from 'react';



export default function HeaderModalComponent({setHeaderModalClose}) {

   

    const onClickHeaderModalClose=(e)=>{
        e.preventDefault();
        setHeaderModalClose();        
    }

    return (
        <div id='headerModal'>
           <div className="container">
            <div className="content">
                <a href="!#"> , <strong>1  </strong> ! <span onClick={onClickHeaderModalClose}><img src="./img/header_modal/close.svg" alt="" /></span></a>                
            </div>
           </div>
        </div>
    );
};
