import React from 'react';
import './scss/intro_modal.scss';
import introModalImage from './images/47ba90e5-40a4-4bfd-b1f6-e8b444942a9d.jpg';



export default function IntroModalComponent ({introModalClocse, cookie}) {

    const {cookieName, cookieValue, cookieExpires} = cookie;  //   


    //     .
    const onClickClose=(e)=>{
        e.preventDefault();
        introModalClocse();  //     
    }

    //    1    .
    const setCookie=()=>{
        let today = new Date();
        today.setDate(today.getDate() + cookieExpires); // 3  = +1        
        document.cookie = `${cookieName}=${cookieValue}; path=/; expires=${today.toUTCString()}`; 
    }

    //     
    const onClickSetCookieClose=(e)=>{
        e.preventDefault();
        setCookie();
        introModalClocse();
    }

    return (
        <div id='introModal'>
            <div className="container">
                <div className="wrap">
                    <div className="content">
                        <ul>
                            <li><a href="!#"><img src={introModalImage} alt="" /></a></li>
                            <li>
                                <button onClick={onClickSetCookieClose} >  </button>
                                <button onClick={onClickClose}></button>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    );
};