import React from 'react';
import {Link, Outlet, useLocation} from 'react-router-dom';

export default function HeaderComponent() {

    const useLoc = useLocation();

    //  
    // 
    // ,           
    const [state, setState] = React.useState({
        isSub1: false,
        isSub2: false,
        isFixed: false 
    });

    // refRow3Fixed useRef();  
    const refRow3Fixed = React.useRef();

    React.useEffect(()=>{
        //  row3   .
        const row3Top = refRow3Fixed.current.offsetTop;
       
        //    
        window.addEventListener('scroll', function(){
            let isFixed = false;
            if( window.scrollY >= row3Top ){
                isFixed = true;
            }
            else{
                isFixed = false;
            }
            setState({
                ...state,
                isFixed: isFixed
            })
        });
    },[]);



    const onMouseEnterNotice=()=>{
        setState({
            ...state,
            isSub1: true
        });
    }
    const onMouseLeaveNotice=()=>{
        setState({
            ...state,
            isSub1: false
        });
    }

    // 
    const onMouseEnterMap=()=>{
        setState({
            ...state,
            isSub2: true
        })
    }

    const onMouseLeaveMap=()=>{
        setState({
            ...state,
            isSub2: false
        })
    }

    return (
        <>
            <header id='header'>
                <div className="row1">
                    <div className="container">
                        <div className="content">
                            <span><Link to="/signUp" className='on'></Link></span>
                            <span><i>|</i></span>
                            <span><Link to="/signIn"></Link></span>
                            <span><i>|</i></span>
                            <span>
                                <Link to="/notice"  onMouseEnter={onMouseEnterNotice}> <img src="./img/intro/ico_down_16x10.png" alt="" /></Link>
                                {
                                state.isSub1 && (<div className="sub" onMouseLeave={onMouseLeaveNotice}>
                                        <ul>
                                            <li><Link to="/notice"></Link></li>
                                            <li><a href="!#"> </a></li>
                                            <li><a href="!#">1:1 </a></li>
                                            <li><a href="!#"> </a></li>
                                        </ul>
                                    </div>)
                                }
                            </span>
                        </div>
                    </div>
                </div>
                <div className={`row2${state.isFixed?' on':''}`}>
                    <div className="container">
                        <div className="content">
                        <div className="left">
                                <span><Link to="/main"><img src="./img/intro/kurly.svg" alt="" /><strong></strong></Link></span>
                                <span><i>|</i></span>
                                <span><a href="!#"></a></span>
                            </div>         
                        <div className="center">
                                <div>
                                    <input type="text" name='input_search' id='inputSearch' placeholder=' '/>
                                    <a href="!#" className='search-btn'><img src="./img/intro/icon_search.svg" alt="" /></a>
                                </div>
                            </div>         
                        <div className="right">
                                <span>
                                    <a href="!#" onMouseEnter={onMouseEnterMap}><img src="./img/intro/icon_map.svg" alt="" /></a>
                                    {
                                        state.isSub2 && (<div className="sub" onMouseLeave={onMouseLeaveMap}>
                                            <ul>
                                                <li><h2> </h2></li>
                                                <li><h3>   !</h3></li>
                                                <li>
                                                    <button className='login'></button><button className='addr-search'><img src="./img/header/icon_search..png" alt="" /></button>
                                                </li>
                                            </ul>
                                        </div>)
                                    }
                                </span>

                                <span><a href="!#"><img src="./img/intro/icon_heart.svg" alt="" /></a></span>
                                <span><a href="!#"><img src="./img/intro/icon_cart.svg" alt="" /></a></span>
                            </div>         
                        </div>
                    </div>
                </div>
                <div ref={refRow3Fixed} className={`row3${state.isFixed?' on':''}`}>

                    <div className="container">
                        <div className="content">
                            <div className="left">
                                <a href="!#"><span></span></a>
                            </div>
                            <div className="center">
                                <nav>
                                    <ul>
                                        <li className={useLoc.pathname==='/sub01'?'on':''}><Link to="/sub01"></Link></li>
                                        <li className={useLoc.pathname==='/sub02'?'on':''}><Link to="/sub02"></Link></li>
                                        <li className={useLoc.pathname==='/sub03'?'on':''}><Link to="/sub03"></Link></li>
                                        <li className={useLoc.pathname==='/sub04'?'on':''}><Link to="/sub04"></Link></li>
                                    </ul>
                                </nav>
                            </div>
                            <div className="right">
                                <a href="!#"><strong></strong><span></span></a>
                            </div>
                        </div>
                    </div>

                </div>
            </header>
            <Outlet />
        </>
    );
};

